.backGround {
  background-image: url("../../assets/home/background-home-desktop.jpg");
  background-size: cover;
  position: absolute;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -10;
}

.textColumn {
  max-width: 445px;
}

.wrapper {
  margin-top: 287px;
  display: flex;
  justify-content: space-around;
}

.text {
  color: #d0d6f9;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

.bigText {
  color: #fff;
  font-family: Bellefair;
  font-size: 150px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.mediumText {
  color: #d0d6f9;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 4.725px;
}

.button {
  margin-top: 60px;

  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 274px;
  height: 274px;
  border-radius: 450px;
  background-color: #fff;

  color: #0b0d17;
  font-family: Bellefair;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
}

.button:hover {
  box-shadow: 0px 0px 0px 80px rgba(255, 255, 255, 0.105);
}
